.menu {
  @include vp-1199 {
    margin-left: 47px;
  }

  @include vp-767 {
    margin-left: unset;
  }
}

.menu__list {
  margin: 32px 0 8px;
  padding: 0;
  list-style: none;

  display: flex;
  flex-direction: row;
  flex-wrap: wrap;
  justify-content: start;
  gap: 22px;
  max-width: 590px;

  @include vp-1199 {
    max-width: 100%;
    gap: 11px;
    margin: 15px 0;
    justify-content: stretch;
  }

  @include vp-767 {
    display: block;
    column-count: 2;
    text-align: start;
    margin: 0;
    max-width: 290px;
    gap: 54px;
    margin-left: -5px;
  }
}

.menu__item {
  padding: 0;
  cursor: pointer;

  &:focus-visible {
    outline: none;
  }

  @include vp-767 {
    margin-bottom: 5px;
  }

  a {
    font-style: normal;
    font-size: 16px;
    font-weight: 400;
    line-height: 16px;
    text-decoration: none;
    color: $color-elephant;
    padding: 5px;

    &:hover {
      border-bottom: 1px $color-elephant solid;
    }

    &:focus,
    &:focus-visible {
      outline: 1px $color-elephant solid;
    }

    &:active {
      outline: none;
      border-bottom: none;
    }

    &:disabled {
      opacity: 0.5;
    }

    @include vp-1199 {
      font-size: 14px;
      font-weight: 400;
      line-height: 14px;
    }
  }
}
